body {
    background-color: honeydew;
}

h1{
    text-align: center;
    font-size: 60px;
}

.title {

}

.pad-button {
    background-color: rgb(48, 167, 48);
    border: none;
    color: white;
    padding: 40px;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    margin: 20px;
    border-radius: 20px;
}

.pad-container {
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 500px;
    height: auto;
    background-color: rgba(129, 129, 129, 0.2);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    border-radius: 15px;
}

.pad-button:active {
    background-color: coral;
}

